import React from 'react';
import './Name.css';

class Name extends React.Component {
  state = {
    text: '',
  };

  handleChange = (e) => {
    const { name, value } = e.target;

    this.setState({
      [name]: value,
    });
  };

  handleSubmit = (e) => {
    e.preventDefault();

    this.props.saveName(this.state.text);

    this.setState({
      text: '',
    });
  };

  render() {
    return (
      <div className="Name">
        <form onSubmit={this.handleSubmit}>
          <span className="text">Hello, What's your name?</span>
          <input
            type="text"
            name="text"
            value={this.state.text}
            onChange={this.handleChange}
          />
        </form>
      </div>
    );
  }
}

export default Name;
