import React from 'react';

import './Search.css';

class Search extends React.Component {
  state = {
    value: '',
    isOpen: false,
  };

  handleSubmit = (e) => {
    e.preventDefault();

    window.location.href = `https://www.google.com/search?q=${this.state.value}&oq=${this.state.value}&aqs=chrome..69i57j35i39j0l6.638j0j15&sourceid=chrome&ie=UTF-8`;

    this.setState({
      ...this.state,
      isOpen: !this.state.isOpen,
    });
  };

  handleClick = () => {
    this.setState({
      ...this.state,
      isOpen: !this.state.isOpen,
    });
  };

  handleChange = (e) => {
    this.setState({
      value: e.target.value,
    });
  };

  render() {
    return (
      <div className="Search">
        <span className="text" onClick={this.handleClick}>
          Search
        </span>
        <form onSubmit={this.handleSubmit}>
          {!this.state.isOpen && (
            <>
              <input
                type="text"
                value={this.state.value}
                onChange={this.handleChange}
              />
            </>
          )}
        </form>
      </div>
    );
  }
}

export default Search;
